/* General Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  background-color: #a2a2a2;
  color: #333;
}

/* App Container - ËÆ© Banner Âõ∫ÂÆöÔºåÊó•ÂéÜÂ°´ÂÖÖÂâ©‰ΩôÈ°µÈù¢ */
.app-container {
  display: flex;
  flex-direction: column;
  height: 100vh; /* ËÆ©Êï¥‰∏™È°µÈù¢Â°´Êª°Á™óÂè£ */
}

/* ============================= */
/* üåü 1. Banner Ê†∑Âºè - ËÆ© Header ÊíëÊª°Êï¥‰∏™È°µÈù¢È°∂ÈÉ® */
/* ============================= */
.banner {
  width: 100%;
  background: #121212; /* ‰∏ªÈ¢òËâ≤ */
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* Â∑¶‰æßÊ†áÈ¢ò */
.banner h1 {
  font-size: 22px;
  font-weight: bold;
  margin: 0;
}

/* Âè≥‰æßÁî®Êà∑ÊåâÈíÆ */
.banner-right {
  display: flex;
  align-items: center;
}

/* Áî®Êà∑ Icon ÊåâÈíÆ */
.user-icon {
  background: none;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  padding: 5px;
  border-radius: 50%;
  transition: background 0.3s;
}

.user-icon:hover {
  background: rgba(255, 255, 255, 0.1);
}

/* Áî®Êà∑Â§¥ÂÉè */
.user-icon img {
  width: 32px;
  height: 32px;
  border-radius: 50%;
}

/* ============================= */
/* üåü 2. Calendar Ê†∑Âºè - ÂÖÖÊª°Êï¥‰∏™Ââ©‰ΩôÈ°µÈù¢ */
/* ============================= */
.calendar-container {
  flex-grow: 1; /* ËÆ© calendar ÂÖÖÊª°Ââ©‰ΩôÁ©∫Èó¥ */
  padding: 20px;
  display: flex;
  flex-direction: column;
  /*overflow-y: auto;  Ê∑ªÂä†ÊªöÂä®Êù°ÔºåÈò≤Ê≠¢Ê∫¢Âá∫ */
}

/* Calendar Header */
.calendar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #202124;
  color: white;
  border-bottom: 1px solid #444;
}

/* Today Button */
.today-button {
  background-color: #1a73e8;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 8px 12px;
  cursor: pointer;
  font-size: 14px;
}

.today-button:hover {
  background-color: #155ab2;
}

/* Month and Year Display */
.month-year-display {
  display: flex;
  align-items: center;
  gap: 10px;
}

.month-year-display h2 {
  margin: 0;
  font-size: 18px;
}

.arrow-button {
  background: none;
  color: white;
  border: none;
  font-size: 20px;
  cursor: pointer;
}

.arrow-button:hover {
  color: #1a73e8;
}

/* ============================= */
/* üåü 3. Calendar Grid Ê†∑Âºè - ËÆ©Êó•ÂéÜ UI Êõ¥Âä†Áé∞‰ª£ */
/* ============================= */
.calendar-weekdays {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  text-align: center;
  font-weight: bold;
  margin-bottom: 10px;
  background-color: #f4f4f4;
  padding: 10px;
  border-radius: 5px;
}

/* Calendar Grid */
.calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 5px;
}

/* Êó•ÂéÜÂç°Áâá */
.calendar-day {
  background: white;
  border-radius: 8px;
  padding: 8px;
  min-height: 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  transition: 0.3s;
  cursor: pointer;
}

.calendar-day:hover {
  background: #f0f0f0;
}

.date-header {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 5px;
}

/* È´ò‰∫ÆÂΩìÂ§©Êó•Êúü */
.calendar-day.today {
  background-color: #1a73e8;
  color: white;
  border: 2px solid #155ab2;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
}

.calendar-day.today:hover {
  background-color: #155ab2;
}

/* ============================= */
/* üåü 4. ‰∫ã‰ª∂ÊòæÁ§∫Âå∫Âüü */
/* ============================= */
.events-container {
  width: 100%;
  text-align: left;
  font-size: 12px;
  padding: 5px;
}

.event {
  background: #242424;
  padding: 3px;
  border-radius: 4px;
  margin-bottom: 3px;
  font-size: 12px;
  text-align: left;
  color:white;
}

.no-events {
  font-size: 12px;
  color: gray;
  text-align: center;
}

/* ============================= */
/* üåü 5. Ê®°ÊÄÅÊ°Ü‰ºòÂåñ (Event Ê∑ªÂä†) */
/* ============================= */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

/* Modal Content */
.modal-content {
  background: #202124;
  padding: 20px;
  border-radius: 8px;
  width: 400px;
  color: white;
  font-size: 14px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
  position: relative;
}

/* Close Button */
.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  color: white;
  font-size: 20px;
  cursor: pointer;
}

/* Ê†áÈ¢ò */
.modal-content h3 {
  margin-bottom: 15px;
}

/* ËæìÂÖ•Ê°ÜÊ†∑Âºè */
.modal-row {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.modal-row label {
  margin-bottom: 5px;
  color: #9aa0a6;
}

.modal-row input,
.modal-row textarea {
  padding: 10px;
  background: #303134;
  border: none;
  border-radius: 4px;
  color: white;
}

.modal-row textarea {
  height: 60px;
  resize: none;
}

/* Save Button */
.save-button {
  background: #1a73e8;
  color: white;
  border: none;
  padding: 12px 20px;
  font-size: 14px;
  border-radius: 4px;
  cursor: pointer;
  width: 100%;
  text-align: center;
}

.save-button:hover {
  background: #0b63ce;
}
